import React, { useState, useEffect } from "react";
import ReactDOM from "react-dom";
import shuffle from "shuffle-array";
import hash from "object-hash";

import "./styles.css";

import { start } from "./Confetti";

function Confetti() {
  useEffect(() => {
    start();
  });
  return <canvas id="canvas" />;
}

function Tile({ id, children, onToggle, isSet }) {
  return (
    <div onClick={onToggle} className={`tile ${isSet ? "tile--set" : ""}`}>
      {children}
    </div>
  );
}

function App() {

  const defaultBoard = {};
  for(var x = 0; x < 25; x++ ) defaultBoard[x] = x;

  const localBoard =  JSON.parse(localStorage.getItem("bingo-board"));
  const localExists =  localBoard !== null && localBoard !== undefined;

  const localChecked = JSON.parse(localStorage.getItem("bingo-checked"));
  const localCheckedExists = localChecked !== null && localBoard !== undefined;
  const defaultState = {checked: localCheckedExists ? localChecked : {}};

  const localCallIndex = JSON.parse(localStorage.getItem("bingo-index"));
  const localCallIndexExists = localCallIndex !== null && localCallIndex !== undefined && localCallIndex > -1;

  const localCallOrder = JSON.parse(localStorage.getItem("bingo-callorder"));
  const localCallOrderExists = localCallOrder !== null && localCallOrder !== undefined;

  const [state, setState] = useState(defaultState);
  const [warning, setWarning] = useState(localExists);
  const [fetched, setFetch] = useState(false);
  const [wordList, setWordList] = useState([]);
  const [board, setBoard] = useState(localExists ? localBoard : defaultBoard);
  const [calledIndex, setCalledIndex] = useState(localCallIndexExists ? localCallIndex : -1);
  const [wordsToCall, setwordsToCall] = useState(localCallOrderExists ? localCallOrder : []); 

  const queryParams = new Proxy(new URLSearchParams(window.location.search), {
    get: (searchParams, prop) => searchParams.get(prop),
  });
  const callerMode = queryParams.caller === "true";
  const calledWords = calledIndex > -1 ? wordsToCall.slice(0, calledIndex +1) : [];



  const isWon = checked => {
    const range = [0, 1, 2, 3, 4];
    return (
      undefined !==
        range.find(row => range.every(column => checked[row * 5 + column])) ||
      undefined !==
        range.find(column => range.every(row => checked[row * 5 + column])) ||
      range.every(index => checked[index * 5 + index]) ||
      range.every(index => checked[index * 5 + 4 - index])
    );
  };


  const toggle = id =>
    setState(state => {
      const checked = { ...state.checked, [id]: !state.checked[id] };
      const won = isWon(checked);
      return {
        ...state,
        checked,
        won
      };
    });

  const callWord = () => {
    if(!(calledIndex + 1 >= wordsToCall.length)) setCalledIndex(calledIndex + 1);
    localStorage.setItem("bingo-index", JSON.stringify(calledIndex));
  };

  const getCurrentCalledWord = () => {
     if(calledIndex + 1 >= wordsToCall.length) return "There Are No More Calls!";
     return calledIndex > -1 ? wordsToCall[calledIndex] : "Press Call To Start";
  }

  const resetCaller = () => {
    if (window.confirm("Are you sure you want to reset? This is an un-recoverable action!") == true) {
       const shuffledList = shuffle(wordList);
       localStorage.setItem("bingo-index", JSON.stringify(-1));
       localStorage.setItem("bingo-callorder", JSON.stringify(shuffledList));
       setwordsToCall(shuffledList);
       setCalledIndex(-1);
    } 
  }


  //Fetch Json
  useEffect(() => {
      if(!fetched){
        fetch('data.json')
        .then((response) => response.json())
        .then((findresponse)=>{
          setWordList(findresponse.data)
        })
        setFetch(true);
      }
  });

  //Generate Unique Board
  useEffect(() => {
    const shuffledList = shuffle(wordList);
  
    if(shuffledList.length >= 25 && !localExists){
      const slicedList = shuffledList.slice(0, 25);
      const mapping = slicedList.reduce((data, value, index) => ({ ...data, [index]: value }),{});
      mapping[12] = "Free";
      setBoard(mapping);
    }
  }, [wordList]);

   //Generate Unique calling order
  useEffect(() => {
    const shuffledList = shuffle(wordList);
    if(!localCallOrderExists  && wordList.length > 0){
      setwordsToCall(shuffledList);
      localStorage.setItem("bingo-callorder", JSON.stringify(shuffledList));
    }
  
  }, [wordList]);


  //Store board in local storage so it doesn't change after each refresh
  useEffect(() => {
    if(board != defaultBoard && !localExists) localStorage.setItem("bingo-board", JSON.stringify(board));
  }, [board]);


  useEffect(() => {
    localStorage.setItem("bingo-checked", JSON.stringify(state.checked));
  }, [state.checked]);


  return !callerMode ? (
    
    <div className="App bingo_back">
    {warning && <div className="alert alert-danger">
      <div className="alert-container">
        <button type="button" className="close-icon" data-dismiss="alert" aria-label="Close" onClick={() => setWarning(false)}>
              <span>clear</span>
        </button>
        <b className="alert-info">&#9888; Anti-Cheat Activated! Your previous board and selections have been restored.  </b>
      </div>
    </div>}
      <h1>Position Management Bingo</h1>
      <h3>Bingo Board Hash: {hash(board)}</h3>
      <h5 className="warning">Refreshing will not yield a new card!</h5> 
      <div className="wrapper">
        {Object.keys(board).map(id => (
          <Tile
            key={id}
            id={id}
            isSet={!!state.checked[id]}
            onToggle={() => toggle(id)}
          >
            {board[id]}
          </Tile>
        ))}
          
        </div>
      {state.won ? <Confetti /> : null}
    </div>
  ) : (
    <div className="App bingo_back2">
      <h1>Position Management Bingo Caller</h1>
      <div className="button-container">
      <button className="button-64" role="button" onClick={() => callWord()}><span className="text">Call</span></button>
      <button className="button-64" role="button" onClick={() => resetCaller()}><span className="text">Reset!</span></button>
      </div>
      <div>
          <h1>Now Calling: {getCurrentCalledWord()} </h1>
      </div>
      <main>
        <ol className="gradient-list">
        {calledWords.map(word => (
          <li key={word}> {word}</li>
        ))}
        </ol>
      </main>
    </div>
  );
}

const rootElement = document.getElementById("root");
ReactDOM.render(<App />, rootElement);
